@import url(https://fonts.googleapis.com/css?family=Oswald|Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,
h2 {
    font-weight: 800; 
    font-family: "Raleway", sans-serif;
}

.jumbotron {
    opacity: 0;
    height: 100vh;
    -webkit-animation: .5s ease-out 1s forwards fade-in;
            animation: .5s ease-out 1s forwards fade-in;
    position: relative;
    display: grid;
    grid-template-columns: 10vw repeat(3, 1fr) 10vw;
    grid-column-gap: 5vw;
    -webkit-column-gap: 5vw;
            column-gap: 5vw;
}

.left {
    grid-column-start: 2;
    grid-row-start: 1;
    text-align: justify; 
    display: flex;
    align-items: center;
    opacity: 0;    
}

.center {
    grid-column-start: 3;
    max-height: 100vh;    
}

.center img:hover {
    opacity: .5;
}

.right {
    grid-column-start: 4;
    grid-row-start: 1;
    text-align: left;
    display: flex;
    align-items: center;
    opacity: 0;
}

.animation-left {
    -webkit-animation: .5 ease-out 1s forwards fade-in-column-left;
            animation: .5 ease-out 1s forwards fade-in-column-left;
}

.animation-right {
    -webkit-animation: .5 ease-out 1s forwards fade-in-column-right;
            animation: .5 ease-out 1s forwards fade-in-column-right;
}

.avatar {
    max-width: 25vw;
    border-radius: 50%;
}

@-webkit-keyframes fade-in {
    0% {
      opacity: 0;
      -webkit-transform: translateY(5vh);
              transform: translateY(5vh);
    }
    100% {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }

@keyframes fade-in {
    0% {
      opacity: 0;
      -webkit-transform: translateY(5vh);
              transform: translateY(5vh);
    }
    100% {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }

@-webkit-keyframes fade-in-column-left {
  0% {
    opacity: 0;
    -webkit-transform: translateX(5vh);
            transform: translateX(5vh);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes fade-in-column-left {
  0% {
    opacity: 0;
    -webkit-transform: translateX(5vh);
            transform: translateX(5vh);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes fade-in-column-right {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-5vh);
            transform: translateX(-5vh);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes fade-in-column-right {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-5vh);
            transform: translateX(-5vh);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

.item {
    border-radius: 3%;
    box-shadow: 0 3px 11px 0 rgba(0,0,0,.18), 0 2px 15px 0 rgba(0,0,0,.15);    
    height: 30vh;
    background-color: rgba(0,0,0,.18);
    opacity: .7;
    transition: 1s;
    padding: 0 .5rem 0 .5rem;    
}

.item:hover {
    opacity: 1;
    background-color: rgba(0, 38, 252, 0.8);
    box-shadow: 0 3px 11px 0 rgba(0,0,0,.5), 0 2px 15px 0 rgba(0,0,0,.4); 
    -webkit-transform: scale(1.1,1.1); 
            transform: scale(1.1,1.1);
}

.item:hover .item:not(.item:hover) {
    opacity: .5;
}

.item-wrapper {
    display: grid;
    grid-template-columns: 15vw repeat(3, 1fr) 15vw;
    grid-column-gap: 5vw;
    -webkit-column-gap: 5vw;
            column-gap: 5vw;
}

.item-1 {
    grid-column-start: 2;    
}

.item-4 {
    grid-column-start: 2;
    grid-row-start: 2;    
}

.item-5 {
    grid-column-start: 3;
    grid-row-start: 2;    
}

.item-6 {
    grid-column-start: 4;
    grid-row-start: 2;    
}
.projects {
    height: 100vh;
}
button:hover {
    background-color: rgba(0, 38, 252, 0.8);
}

.cv:hover,
.logo:hover {
    color: rgba(0, 38, 252, 0.8);
}

.footer {
    height: 100vh;
    position: relative;  
    display: flex;
    flex-direction: column;
    align-items: center;
}

.social {
    display: flex;
    flex-direction: row; 
    margin: 10vh 10vw;   
}

.social div {
    width: 4vw;
}

.logo {
    max-width: 3vw;
    margin: .5vw;
}

.cv {
    display: flex;
    flex-direction: column;    
    max-height: 10vh;
    margin: 10vh auto;
}

/* modal */

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  background-color: rgba(0, 0, 0, 0.8);
}

.modal-content {
  position: fixed;
  display: flex;
  justify-content: center;
  padding: 2.5em 1.5em 1.5em 1.5em;
  background-color: rgba(255,255,255);
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 30vw;
  height: 80vh; 
  border-radius: 5%; 
}

.modal .close-modal {
    position: absolute;
    top: 0;
    right: 0;
    margin: 1rem;
    background-color: rgba(255,255,255);
    color: rgba(0,0,0, .8);
    max-height: 1.5rem;
    max-width: 1.5rem;
    padding: .1rem;
    height: 100%;
    width: 100%;
    border-radius: 50%;
}

/* form */

.form {    
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5vh;
    width: 25vw;
    height: 75vh;
}

.form input,
.form textarea {
    margin: 2vh 0 2vh 0;
    box-shadow: 0 3px 11px 0 rgba(0,0,0,.18), 0 2px 15px 0 rgba(0,0,0,.15);
    border-radius: 5%;
    border: none;
    font-family: "Oswald", sans-serif; 
}

.form textarea {
    min-height: 25vh;
}

.form input {
    height: 4vh;
    border-radius: 3%;    
}

.input-field {
    display: flex;
    flex-direction: column;
}

.hidden-field {
    display: none;
}

.form input[type="submit"] {
    border-radius: 10%;    
    background-color: rgba(0,0,0, .8);
    color: rgba(255,255,255, .8);
    max-width: 5vw;
    cursor: pointer;
}


a {
  text-decoration: none;
   color: rgba(0, 0, 0, .8);
}

ul {
  list-style: none;
}

button {
  border: none;
  border-radius: 5%; 
  padding: .5rem;
  background-color: rgba(0,0,0, .8);
  color: rgb(255,255,255);
  max-width: 8vw;
  cursor: pointer;
  text-transform: uppercase;
}

.App {
  text-align: center; 
  font-family: "Oswald", sans-serif;  
}

.arrow {
  width: 5vw;
  opacity: .7;
  margin-top: 5vh;
}

.arrow:hover {
  color: rgba(0, 38, 252, 0.8);
}

.scroll-lock {
  overflow: hidden;
}



@media  (max-width: 1024px) {
  /* jumbotron-mobile */
  .jumbotron,
  .projects,
  .footer {
    height: auto;
  }
  .jumbotron {
    display: grid;
    grid-template-columns: 15vw 1fr 15vw;
    grid-column-gap: 0;
    -webkit-column-gap: 0;
            column-gap: 0;
  }

  .left,
  .center,
  .right {
    grid-row-start: 1;
    grid-column-start: 2;
    grid-column-end: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 1; 
    height: 80vh;
  }

  .left {
    grid-row-start: 2;
  }

  .center {
    grid-row-start: 1;
    display: flex;
    flex-direction: column;
  }

  .center img:hover {
    opacity: 1;
  }

  .right {
    grid-row-start: 3;
  }

/* projects-mobile */

  .item-wrapper {
    display: grid;
    grid-template-columns: 15vw 1fr 15vw;
    grid-column-gap: 0;
    -webkit-column-gap: 0;
            column-gap: 0;
  }

  .item-wrapper a {
    grid-column-start: 2;
    grid-row-start: auto; 
  }

  .item-wrapper a {
    grid-column-start: 2;
  }

  /* footer-mobile */

  .social {
    display: flex;
    flex-direction: row; 
    margin: 5vh 0;   
  }

  .social div {
      width: 15vw;
      margin: 0 4vw;
  }

  .logo {
      max-width: 100%;
      margin: 0;
  }

  .open-modal-btn {
    max-width: 25vw;
  }

  .cv {
      display: flex;
      flex-direction: column;  
      justify-content: center;  
      max-height: 20vh;
      width: 10vw;
      margin: 10vh 0;
  }

  /* modal-mobile */
  .modal-content {
    width: 50vw;
    height: auto; 
  }

  .form input[type="submit"] { 
    border-radius: 5%;
    width: auto;  
    max-width: 15vw;    
}
}
